ACA,uBAAA;IACA,mBAAA;IACA,aAAA;IACA,aAAA;IACA,oCAAA;IACA,kBAAA;AACA;AAEA;IACA,WAAA;IACA,YAAA;IACA,YAAA;IACA,gBAAA;AACA;AAEA;IACA,eAAA;IACA,iBAAA;IACA,mBAAA;IACA,aAAA;AACA;;AAEA,uBAAA;AACA;IACA,uBAAA;IACA,qBAAA;AACA;AAEA;IACA,wBAAA;AACA;AAEA;IACA,0BAAA;AACA;;AAEA,uBAAA;AACA;IACA,sBAAA;IACA,+BAAA;IACA,6BAAA;IACA,yBAAA;IACA,6BAAA;IACA,sBAAA;IACA,uBAAA;AACA;AAEA;IACA,WAAA;IACA,YAAA;IACA,mBAAA;IACA,sBAAA;AACA;;AAEA,kDAAA;AACA;IACA,wBAAA;AACA;;AAEA,sBAAA;AACA;IACA,aAAA;IACA,mBAAA;IACA,SAAA;IACA,0BAAA;AACA;;AAEA,4BAAA;AACA;IACA,iBAAA;IACA,mBAAA;IACA,oCAAA;AACA;AAEA;IACA,2BAAA;AACA;;AAEA,uCAAA;AACA;IACA,oBAAA;IACA,6CAAA;IACA,QAAA;IACA,YAAA;AACA;AAEA;IACA,WAAA;IACA,gBAAA;AACA;;AAEA,wBAAA;AACA;IACA,iBAAA;IACA,eAAA;IACA,gBAAA;IACA,WAAA;IACA,YAAA;IACA,oBAAA;IACA,mBAAA;IACA,kBAAA;IACA,uDAAA;IACA,2CAAA;CACA,yBAAA;AACA;AAEA;IACA,qBAAA;IACA,2CAAA;IACA,wDAAA;AACA;AAEA;IACA,UAAA;AACA;AAEA;IACA,YAAA;IACA,kBAAA;AACA;;AAEA,oBAAA;AACA;IACA,YAAA;IACA,kBAAA;AACA;;AAEA,mCAAA;AACA;IACA,eAAA;IACA,gBAAA;IACA,6BAAA;IACA,YAAA;IACA,eAAA;IACA,YAAA;IACA,mBAAA;IACA,kBAAA;AACA;AAEA;IACA,UAAA;IACA,+CAAA;AACA;AAEA;IACA,UAAA;IACA,mBAAA;AACA;AAEA;;IAEA,yBAAA;AACA;AAEA;;IAEA,UAAA;IACA,mBAAA;AACA;AAEA;IACA,oBAAA;IACA,mBAAA;IACA,aAAA;AACA;AAEA;IACA,0BAAA;AACA;;AAEA,8BAAA;AACA;IACA,aAAA;IACA,sBAAA;IACA,aAAA;IACA,kBAAA;IACA,aAAA;IACA,uBAAA;AACA;;AAEA,wBAAA;AACA;IACA,iBAAA;IACA,kBAAA;IACA,oCAAA;AACA;;AAEA,oCAAA;AACA;IACA,aAAA;IACA,oCAAA;AACA;;AAEA,gDAAA;AACA;IACA,gBAAA;AACA;;AAEA,qCAAA;AACA;IACA,qBAAA;IACA,iBAAA;IACA,qBAAA;IACA,mCAAA;IACA,sCAAA;IACA,gCAAA;AACA;;AAEA,mCAAA;AACA;IACA,oDAAA;AACA;;AAEA,4BAAA;AACA;IACA,gBAAA;IACA,iBAAA;IACA,yCAAA;AACA;AAEA;IACA,mBAAA;IACA,oCAAA;AACA;;AAEA,iCAAA;AACA;IACA,mBAAA;AACA;AAEA;IACA,mBAAA;IACA,oCAAA;AACA;AAEA;IACA,gBAAA;AACA;AAEA;IACA,qCAAA;IACA,mCAAA;IACA,aAAA;IACA,mBAAA;IACA,8CAAA;AACA;AAEA;IACA,aAAA;IACA,8BAAA;IACA,mBAAA;IACA,mBAAA;AACA;AAEA;IACA,SAAA;IACA,oCAAA;AACA;AAEA;IACA,aAAA;IACA,SAAA;AACA;AAEA;IACA,iBAAA;IACA,eAAA;AACA;AAEA;IACA,aAAA;IACA,8BAAA;AACA;AAEA;IACA,kBAAA;IACA,aAAA;IACA,8BAAA;IACA,sCAAA;IACA,mCAAA;AACA;;AAEA,iBAAA;AACA;IACA,eAAA;IACA,MAAA;IACA,OAAA;IACA,WAAA;IACA,YAAA;IACA,oCAAA;IACA,aAAA;IACA,uBAAA;IACA,mBAAA;IACA,aAAA;AACA;AAEA;IACA,8CAAA;IACA,mCAAA;IACA,UAAA;IACA,gBAAA;IACA,gBAAA;IACA,gBAAA;IACA,yCAAA;AACA;AAEA;IACA,aAAA;IACA,8BAAA;IACA,mBAAA;IACA,aAAA;IACA,4CAAA;AACA;AAEA;IACA,SAAA;IACA,oCAAA;AACA;AAEA;IACA,gBAAA;IACA,YAAA;IACA,eAAA;IACA,eAAA;IACA,8BAAA;IACA,UAAA;IACA,WAAA;IACA,YAAA;IACA,aAAA;IACA,mBAAA;IACA,uBAAA;AACA;AAEA;IACA,oCAAA;AACA;AAEA;IACA,kBAAA;AACA;AAEA;IACA,aAAA;IACA,yBAAA;IACA,SAAA;IACA,aAAA;IACA,yCAAA;AACA;AAEA;IACA,aAAA;IACA,SAAA;AACA;AAEA;IACA,gBAAA;IACA,oCAAA;AACA;AAEA;;IAEA,WAAA;IACA,gBAAA;IACA,iBAAA;IACA,qCAAA;IACA,mCAAA;IACA,8CAAA;IACA,8BAAA;AACA;AAEA;;IAEA,aAAA;IACA,kCAAA;AACA;;AAEA,mDAAA;AACA;IACA,gBAAA;IACA,WAAA;AACA;AAEA;IACA,aAAA;IACA,SAAA;IACA,mBAAA;AACA;AAEA;IACA,OAAA;AACA;AAEA;IACA,WAAA;IACA,YAAA;IACA,YAAA;IACA,mCAAA;IACA,eAAA;AACA;;AAEA,2BAAA;AACA;AACA;QACA,UAAA;QACA,YAAA;AACA;AAEA;QACA,sBAAA;QACA,uBAAA;QACA,SAAA;AACA;AAEA;QACA,WAAA;QACA,yBAAA;AACA;AACA;;AAEA,kCAAA;AACA;IACA,aAAA;IACA,sBAAA;IACA,mBAAA;IACA,gBAAA;IACA,sBAAA;IACA,yCAAA;AACA;AAEA;IACA,kBAAA;IACA,mBAAA;AACA;;AAEA,mCAAA;AACA;AACA;QACA,sBAAA;AACA;AAEA;QACA,sBAAA;QACA,gBAAA;AACA;AACA","sourcesContent":["<template>\r\n    <div id=\"iframewidget-admin-settings\" class=\"section\">\r\n        <!-- Header section -->\r\n        <div class=\"iframewidget-header\">\r\n            <h2>{{ t('iframewidget', 'iFrame Widget Settings') }}</h2>\r\n            <div class=\"iframewidget-logo\">\r\n                <a href=\"https://github.com/IT-BAER\" target=\"_blank\" rel=\"noopener noreferrer\" class=\"logo-link\">\r\n                    <img src=\"../../img/baer4-100x100.png\" alt=\"Logo\" class=\"iframewidget-logo-image\">\r\n                </a>\r\n                <a href=\"https://github.com/IT-BAER/nc-iframewidget/releases\" target=\"_blank\" rel=\"noopener noreferrer\" class=\"version-link\">\r\n                    <span class=\"iframewidget-version\">v0.8.0</span>\r\n                </a>\r\n            </div>\r\n        </div>\r\n        \r\n        <p class=\"settings-hint\">\r\n            {{ t('iframewidget', 'Configure the iFrame Widget for the Dashboard.') }}\r\n        </p>\r\n        \r\n        <!-- Public iFrame Widget Section -->\r\n        <div class=\"iframewidget-public-section\">\r\n            <h3>{{ t('iframewidget', 'Public iFrame Widget') }}</h3>\r\n            <p class=\"settings-hint\">\r\n                {{ t('iframewidget', 'Configure the main iFrame widget visible to all users.') }}\r\n            </p>\r\n        </div>\r\n        \r\n        <div class=\"iframewidget-admin-container\">\r\n            <!-- Left side: Settings form -->\r\n            <div class=\"iframewidget-admin-form\">\r\n                <div class=\"iframewidget-grid-form\">\r\n                    <!-- Widget Title -->\r\n                    <label for=\"iframe-widget-title\">\r\n                        {{ t('iframewidget', 'Widget Title') }}\r\n                    </label>\r\n                    <input id=\"iframe-widget-title\"\r\n                        v-model=\"state.widgetTitle\"\r\n                        type=\"text\"\r\n                        :placeholder=\"t('iframewidget', 'Hidden')\">\r\n                    \r\n                    <!-- Widget Icon -->\r\n                    <label for=\"widget-icon\">\r\n                        {{ t('iframewidget', 'Widget Icon') }}\r\n\t\t\t\t\t\t<span class=\"icon-finder\">\r\n\t\t\t\t\t\t\t- {{ t('iframewidget', 'Find icons at') }} \r\n\t\t\t\t\t\t\t<a href=\"https://simpleicons.org/\" \r\n\t\t\t\t\t\t\t   target=\"_blank\" \r\n\t\t\t\t\t\t\t   rel=\"noopener noreferrer\">\r\n\t\t\t\t\t\t\t   simpleicons.org\r\n\t\t\t\t\t\t\t</a>\r\n\t\t\t\t\t\t</span>\r\n                    </label>\r\n                    <div class=\"icon-input-container\">\r\n\t\t\t\t\t\t<input id=\"widget-icon\"\r\n    \t\t\t\t\t\tv-model=\"typedIcon\"\r\n    \t\t\t\t\t\ttype=\"text\"\r\n    \t\t\t\t\t\t@input=\"debounceIconUpdate\"\r\n    \t\t\t\t\t\t:placeholder=\"t('iframewidget', 'si:github or si:nextcloud')\">\r\n\t\t\t\t\t\t<input type=\"color\" \r\n\t\t\t\t\t\t\t   :value=\"colorValue\" \r\n\t\t\t\t\t\t\t   @input=\"updateColor\" \r\n\t\t\t\t\t\t\t   :disabled=\"!state.widgetIcon || !state.widgetIcon.startsWith('si:')\"\r\n\t\t\t\t\t\t\t   class=\"color-picker\">\r\n                            \r\n                        <div class=\"color-button-container\" :class=\"{'has-button': state.widgetIconColor && state.widgetIconColor !== ''}\">\r\n                            <transition name=\"fade-scale\">\r\n                                <button v-if=\"state.widgetIconColor && state.widgetIconColor !== ''\" \r\n                                        type=\"button\"\r\n                                        class=\"icon-delete icon-reset-color\" \r\n                                        @click=\"clearColor\"\r\n                                        :title=\"t('iframewidget', 'Reset color')\">\r\n                                </button>\r\n                            </transition>\r\n                        </div>\r\n                    </div>\r\n\r\n                    <!-- URL to Display section -->\r\n                    <label for=\"iframeUrl\">{{ t('iframewidget', 'URL to Display') }}</label>\r\n                    <input type=\"text\" \r\n                        :value=\"typedUrl\"\r\n                        @input=\"handleUrlInput\"\r\n                        id=\"iframeUrl\" \r\n                        class=\"iframewidget-input\" \r\n                        name=\"iframeUrl\" \r\n                        :placeholder=\"t('iframewidget', 'https://example.org')\">\r\n\r\n\r\n                    <!-- iFrame Height -->\r\n                    <label for=\"iframe-height\">\r\n                        {{ t('iframewidget', 'iFrame Height (px)') }}\r\n                    </label>\r\n                    <input id=\"iframe-height\"\r\n                        v-model=\"state.iframeHeight\"\r\n                        type=\"number\"\r\n                        min=\"0\"\r\n                        :placeholder=\"t('iframewidget', '100%')\">\r\n\r\n                    <!-- Extra Wide Toggle -->\r\n                    <label for=\"extra-wide\" class=\"checkbox-label\">\r\n                        {{ t('iframewidget', 'Extra Wide (2 Col)') }}\r\n                    </label>\r\n                    <div class=\"checkbox-container\">\r\n                        <label>\r\n                            <input id=\"extra-wide\"\r\n                                v-model=\"extraWideComputed\"\r\n                                type=\"checkbox\"\r\n                                class=\"checkbox\">\r\n                            <span class=\"checkbox-icon\"></span>\r\n                        </label>\r\n                    </div>\r\n                </div>\r\n\r\n                <!-- Save Button -->\r\n                <div class=\"iframewidget-button-group\">\r\n                    <button @click=\"saveSettings\" class=\"primary\">\r\n                        {{ t('iframewidget', 'Save') }}\r\n                    </button>\r\n                </div>\r\n            </div>\r\n\r\n            <!-- Right side: Widget preview -->\r\n            <div class=\"iframewidget-admin-preview\">\r\n                <h4 class=\"preview-title\">{{ t('iframewidget', 'Preview') }}</h4>\r\n                <div class=\"preview-container\" :style=\"{ width: isExtraWide ? '640px' : '320px' }\">\r\n                    <div class=\"preview-header\" :class=\"{'preview-title-empty': !state.widgetTitle || state.widgetTitle.trim() === ''}\">\r\n                        <h2>\r\n                            <span v-if=\"state.widgetIcon && state.widgetTitle && state.widgetTitle.trim() !== ''\"\r\n                                class=\"widget-icon\">\r\n                                <img :src=\"getIconUrl(state.widgetIcon, state.widgetIconColor)\" \r\n                                    :alt=\"state.widgetIcon\" \r\n                                    @error=\"handleIconError\"\r\n                                    class=\"dashboard-icon\">\r\n                            </span>\r\n                            <span class=\"icon-iframewidget\" v-else-if=\"state.widgetTitle && state.widgetTitle.trim() !== ''\"></span>\r\n                            <span v-if=\"state.widgetTitle && state.widgetTitle.trim() !== ''\">{{ state.widgetTitle }}</span>\r\n                        </h2>\r\n                    </div>\r\n                    <div class=\"preview-content\" :class=\"{'preview-title-empty': !state.widgetTitle || state.widgetTitle.trim() === ''}\">\r\n                        <div v-if=\"!state.iframeUrl\" class=\"preview-empty\">\r\n                            {{ t('iframewidget', 'No URL configured. Please set a URL in the Settings.') }}\r\n                        </div>\r\n\r\n\t\t\t\t\t\t<!-- CSP Error state - Shows helpful guidance when content is blocked by CSP -->\r\n\t\t\t\t\t\t<div v-else-if=\"iframeError && state.iframeUrl\" class=\"widget-error csp-error\">\r\n\t\t\t\t\t\t\t<div class=\"error-title\">{{ t('iframewidget', 'Failed to load content') }}</div>\r\n\t\t\t\t\t\t\t<p>{{ t('iframewidget', 'This might be caused by Content Security Policy (CSP) restrictions.') }}</p>\r\n\t\t\t\t\t\t\t<div class=\"error-actions\">\r\n\t\t\t\t\t\t\t\t<a href=\"https://github.com/IT-BAER/nc-iframewidget#csp-configuration\" target=\"_blank\" class=\"button primary\">\r\n\t\t\t\t\t\t\t\t\t{{ t('iframewidget', 'View CSP Configuration Guide') }}\r\n\t\t\t\t\t\t\t\t</a>\r\n\t\t\t\t\t\t\t</div>\r\n\t\t\t\t\t\t</div>\r\n\t\t\t\t\t\t<iframe v-else\r\n\t\t\t\t\t\t\t\t:src=\"state.iframeUrl\"\r\n\t\t\t\t\t\t\t\t:style=\"{ height: previewHeight }\"\r\n\t\t\t\t\t\t\t\tclass=\"preview-frame\"\r\n\t\t\t\t\t\t\t\treferrerpolicy=\"no-referrer\"\r\n\t\t\t\t\t\t\t\tallow=\"fullscreen\"\r\n\t\t\t\t\t\t\t\t@error=\"handleIframeError\"\r\n\t\t\t\t\t\t\t\t@load=\"iframeError = false\"\r\n\t\t\t\t\t\t\t\tsandbox=\"allow-same-origin allow-scripts allow-popups allow-forms\">\r\n\t\t\t\t\t\t</iframe>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </div>\r\n\r\n        <!-- Group-based iFrame Widgets Section -->\r\n        <div class=\"iframewidget-group-section\">\r\n            <h3>{{ t('iframewidget', 'Group-based iFrame Widgets') }}</h3>\r\n            <p class=\"settings-hint\">\r\n                {{ t('iframewidget', 'Create iFrame widgets that are only visible to specific user groups.') }}\r\n            </p>\r\n\r\n            <!-- Add New Group Widget Button -->\r\n            <div class=\"iframewidget-button-group\">\r\n                <button @click=\"showAddGroupDialog\" class=\"primary\">\r\n                    {{ t('iframewidget', 'Add Group Widget') }}\r\n                </button>\r\n            </div>\r\n\r\n            <!-- Group Widgets List -->\r\n            <div v-if=\"groupWidgets.length > 0\" class=\"group-widgets-list\">\r\n                <!-- Group widgets by groupId -->\r\n                <div v-for=\"(widgets, groupId) in groupedWidgets\" :key=\"groupId\" class=\"group-section\">\r\n                    <div class=\"group-header\">\r\n                        <h4>{{ getGroupDisplayName(groupId) }}</h4>\r\n                        <button @click=\"addWidgetToGroup(groupId)\" class=\"button small\">\r\n                            {{ t('iframewidget', 'Add Widget') }}\r\n                        </button>\r\n                    </div>\r\n                    <div class=\"widgets-in-group\">\r\n                        <div v-for=\"widget in widgets\" :key=\"widget.id\" class=\"group-widget-item\" :class=\"{ 'is-default': widget.isDefault }\">\r\n                            <div class=\"widget-header\">\r\n                                <div class=\"widget-title\">\r\n                                    <span v-if=\"widget.isDefault\" class=\"default-indicator\" title=\"Default widget\">★</span>\r\n                                    <span>{{ widget.title || t('iframewidget', 'Untitled Widget') }}</span>\r\n                                </div>\r\n                                <div class=\"widget-actions\">\r\n                                    <button v-if=\"!widget.isDefault\" @click=\"setAsDefault(widget.id)\" class=\"button small\" title=\"Set as default\">\r\n                                        {{ t('iframewidget', 'Set Default') }}\r\n                                    </button>\r\n                                    <button @click=\"editGroupWidget(widget)\" class=\"button small\">\r\n                                        {{ t('iframewidget', 'Edit') }}\r\n                                    </button>\r\n                                    <button @click=\"deleteGroupWidget(widget.id)\" class=\"button small button-danger\">\r\n                                        {{ t('iframewidget', 'Delete') }}\r\n                                    </button>\r\n                                </div>\r\n                            </div>\r\n                            <div class=\"widget-details\">\r\n                                <p><strong>{{ t('iframewidget', 'URL:') }}</strong> {{ widget.url || t('iframewidget', 'Not set') }}</p>\r\n                                <p><strong>{{ t('iframewidget', 'Icon:') }}</strong> {{ widget.icon || t('iframewidget', 'Default') }}</p>\r\n                            </div>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n            <div v-else class=\"no-group-widgets\">\r\n                <p>{{ t('iframewidget', 'No group widgets configured yet.') }}</p>\r\n            </div>\r\n        </div>\r\n\r\n        <!-- Group Widget Dialog -->\r\n        <div v-if=\"showGroupDialog\" class=\"modal-overlay\" @click=\"hideGroupDialog\">\r\n            <div class=\"modal-content\" @click.stop>\r\n                <div class=\"modal-header\">\r\n                    <h3>{{ isEditing ? t('iframewidget', 'Edit Group Widget') : t('iframewidget', 'Add Group Widget') }}</h3>\r\n                    <button @click=\"hideGroupDialog\" class=\"modal-close\">&times;</button>\r\n                </div>\r\n                <div class=\"modal-body\">\r\n                    <div class=\"iframewidget-grid-form\">\r\n                        <!-- Group Selection -->\r\n                        <label for=\"group-select\">{{ t('iframewidget', 'Select Group') }}</label>\r\n                        <select id=\"group-select\" v-model=\"selectedGroupId\" :disabled=\"isEditing\">\r\n                            <option value=\"\">{{ t('iframewidget', 'Choose a group...') }}</option>\r\n                            <option v-for=\"group in availableGroups\" :key=\"group.id\" :value=\"group.id\">\r\n                                {{ group.displayName || group.id }}\r\n                            </option>\r\n                        </select>\r\n\r\n                        <!-- Widget Title -->\r\n                        <label for=\"group-widget-title\">{{ t('iframewidget', 'Widget Title') }}</label>\r\n                        <input id=\"group-widget-title\"\r\n                            v-model=\"groupWidgetForm.title\"\r\n                            type=\"text\"\r\n                            :placeholder=\"t('iframewidget', 'Group iFrame Widget')\">\r\n\r\n                        <!-- Widget Icon -->\r\n                        <label for=\"group-widget-icon\">{{ t('iframewidget', 'Widget Icon') }}\r\n\t\t\t\t\t\t\t<span class=\"icon-finder\">\r\n\t\t\t\t\t\t\t\t- {{ t('iframewidget', 'Find icons at') }} \r\n\t\t\t\t\t\t\t\t<a href=\"https://simpleicons.org/\" \r\n\t\t\t\t\t\t\t\t   target=\"_blank\" \r\n\t\t\t\t\t\t\t\t   rel=\"noopener noreferrer\">\r\n\t\t\t\t\t\t\t\t   simpleicons.org\r\n\t\t\t\t\t\t\t\t</a>\r\n\t\t\t\t\t\t\t</span>\r\n                        </label>\r\n                        <div class=\"icon-input-container\">\r\n                            <input id=\"group-widget-icon\"\r\n                                v-model=\"groupWidgetForm.icon\"\r\n                                type=\"text\"\r\n                                :placeholder=\"t('iframewidget', 'si:nextcloud')\">\r\n                            <input type=\"color\"\r\n                                :value=\"groupWidgetForm.iconColor || '#ffffff'\"\r\n                                @input=\"updateGroupColor\"\r\n                                :disabled=\"!groupWidgetForm.icon || !groupWidgetForm.icon.startsWith('si:')\"\r\n                                class=\"color-picker\">\r\n                            <div class=\"color-button-container\" :class=\"{'has-button': groupWidgetForm.iconColor && groupWidgetForm.iconColor !== ''}\">\r\n                                <transition name=\"fade-scale\">\r\n                                    <button v-if=\"groupWidgetForm.iconColor && groupWidgetForm.iconColor !== ''\" \r\n                                            type=\"button\"\r\n                                            class=\"icon-delete icon-reset-color\" \r\n                                            @click=\"clearGroupColor\"\r\n                                            :title=\"t('iframewidget', 'Reset color')\">\r\n                                    </button>\r\n                                </transition>\r\n                            </div>\r\n                        </div>\r\n\r\n                        <!-- URL -->\r\n                        <label for=\"group-iframe-url\">{{ t('iframewidget', 'URL to Display') }}</label>\r\n                        <input id=\"group-iframe-url\"\r\n                            v-model=\"groupWidgetForm.url\"\r\n                            type=\"text\"\r\n                            :placeholder=\"t('iframewidget', 'https://example.org')\">\r\n\r\n                        <!-- Height -->\r\n                        <label for=\"group-iframe-height\">{{ t('iframewidget', 'iFrame Height (px)') }}</label>\r\n                        <input id=\"group-iframe-height\"\r\n                            v-model=\"groupWidgetForm.height\"\r\n                            type=\"number\"\r\n                            min=\"0\"\r\n                            :placeholder=\"t('iframewidget', '100%')\">\r\n\r\n                        <!-- Extra Wide -->\r\n                        <label for=\"group-extra-wide\" class=\"checkbox-label\">\r\n                            {{ t('iframewidget', 'Extra Wide (2 Col)') }}\r\n                        </label>\r\n                        <div class=\"checkbox-container\">\r\n                            <label>\r\n                                <input id=\"group-extra-wide\"\r\n                                    v-model=\"groupWidgetForm.extraWide\" \r\n                                    type=\"checkbox\" \r\n                                    class=\"checkbox\"\r\n                                    :true-value=\"true\"\r\n                                    :false-value=\"false\">\r\n                                <span class=\"checkbox-icon\"></span>\r\n                            </label>\r\n                        </div>\r\n\r\n                        <!-- Set as Default -->\r\n                        <label for=\"group-is-default\" class=\"checkbox-label\">\r\n                            {{ t('iframewidget', 'Set as Default Widget') }}\r\n                        </label>\r\n                        <div class=\"checkbox-container\">\r\n                            <label>\r\n                                <input id=\"group-is-default\"\r\n                                    v-model=\"groupWidgetForm.isDefault\" \r\n                                    type=\"checkbox\" \r\n                                    class=\"checkbox\"\r\n                                    :true-value=\"true\"\r\n                                    :false-value=\"false\">\r\n                                <span class=\"checkbox-icon\"></span>\r\n                            </label>\r\n                            <small class=\"help-text\">{{ t('iframewidget', 'Users in this group will see this widget by default') }}</small>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n\r\n                <!-- Widget Preview -->\r\n                <div class=\"modal-preview-section\">\r\n                    <h4 class=\"preview-title\">{{ t('iframewidget', 'Widget Preview') }}</h4>\r\n                    <div class=\"preview-container\" :style=\"{ width: groupWidgetForm.extraWide ? '640px' : '320px' }\">\r\n                        <div class=\"preview-header\" :class=\"{'preview-title-empty': !groupWidgetForm.title || groupWidgetForm.title.trim() === ''}\">\r\n                            <h2>\r\n                                <span v-if=\"groupWidgetForm.icon && groupWidgetForm.title && groupWidgetForm.title.trim() !== ''\"\r\n                                    class=\"widget-icon\">\r\n                                    <img :src=\"getIconUrl(groupWidgetForm.icon, groupWidgetForm.iconColor)\" \r\n                                        :alt=\"groupWidgetForm.icon\" \r\n                                        @error=\"handleIconError\"\r\n                                        class=\"dashboard-icon\">\r\n                                </span>\r\n                                <span class=\"icon-iframewidget\" v-else-if=\"groupWidgetForm.title && groupWidgetForm.title.trim() !== ''\"></span>\r\n                                <span v-if=\"groupWidgetForm.title && groupWidgetForm.title.trim() !== ''\">{{ groupWidgetForm.title }}</span>\r\n                            </h2>\r\n                        </div>\r\n                        <div class=\"preview-content\" :class=\"{'preview-title-empty': !groupWidgetForm.title || groupWidgetForm.title.trim() === ''}\">\r\n                            <div v-if=\"!groupWidgetForm.url\" class=\"preview-empty\">\r\n                                {{ t('iframewidget', 'No URL configured. Please set a URL in the Settings.') }}\r\n                            </div>\r\n\r\n                            <!-- CSP Error state - Shows helpful guidance when content is blocked by CSP -->\r\n                            <div v-else-if=\"groupIframeError && groupWidgetForm.url\" class=\"widget-error csp-error\">\r\n                                <div class=\"error-title\">{{ t('iframewidget', 'Failed to load content') }}</div>\r\n                                <p>{{ t('iframewidget', 'This might be caused by Content Security Policy (CSP) restrictions.') }}</p>\r\n                                <div class=\"error-actions\">\r\n                                    <a href=\"https://github.com/IT-BAER/nc-iframewidget#csp-configuration\" target=\"_blank\" class=\"button primary\">\r\n                                        {{ t('iframewidget', 'View CSP Configuration Guide') }}\r\n                                    </a>\r\n                                </div>\r\n                            </div>\r\n                            <iframe v-else\r\n                                    :src=\"groupWidgetForm.url\"\r\n                                    :style=\"{ height: groupPreviewHeight }\"\r\n                                    class=\"preview-frame\"\r\n                                    referrerpolicy=\"no-referrer\"\r\n                                    allow=\"fullscreen\"\r\n                                    @error=\"handleGroupIframeError\"\r\n                                    @load=\"groupIframeError = false\"\r\n                                    sandbox=\"allow-same-origin allow-scripts allow-popups allow-forms\">\r\n                            </iframe>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n\r\n                <div class=\"modal-footer\">\r\n                    <button @click=\"hideGroupDialog\" class=\"button\">\r\n                        {{ t('iframewidget', 'Cancel') }}\r\n                    </button>\r\n                    <button @click=\"saveGroupWidget\" class=\"button primary\" :disabled=\"!selectedGroupId\">\r\n                        {{ isEditing ? t('iframewidget', 'Update') : t('iframewidget', 'Create') }}\r\n                    </button>\r\n                </div>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nimport axios from '@nextcloud/axios'\r\nimport { generateUrl } from '@nextcloud/router'\r\nimport { loadState } from '@nextcloud/initial-state'\r\nimport { showSuccess, showError } from '@nextcloud/dialogs'\r\n\r\nexport default {\r\n    name: 'AdminSettings',\r\n    data() {\r\n        const loadedState = loadState('iframewidget', 'admin-config');\r\n        const defaultState = {\r\n            widgetTitle: 'iFrame Widget',\r\n            widgetIcon: 'si:nextcloud',\r\n            widgetIconColor: '',\r\n            extraWide: false,\r\n            maxSize: false,\r\n            iframeUrl: '',\r\n            iframeHeight: ''\r\n        };\r\n\r\n        // Ensure we always have a complete reactive state object\r\n        // Merge loaded state with defaults to prevent undefined properties\r\n        const state = loadedState && typeof loadedState === 'object'\r\n            ? { ...defaultState, ...loadedState }\r\n            : { ...defaultState };\r\n\r\n        return {\r\n            state: state,\r\n            typedUrl: '',\r\n            typedIcon: '',\r\n            urlUpdateTimer: null,\r\n            iconUpdateTimer: null,\r\n            loading: false,\r\n            isLoading: false,\r\n            iframeError: false,\r\n            // Group management data\r\n            groupWidgets: [],\r\n            availableGroups: [],\r\n            showGroupDialog: false,\r\n            isEditing: false,\r\n            selectedGroupId: '',\r\n            editingWidgetId: '', // Store the widget ID when editing\r\n            groupWidgetForm: {\r\n                id: '',\r\n                title: '',\r\n                icon: '',\r\n                iconColor: '',\r\n                url: '',\r\n                height: '',\r\n                extraWide: false,\r\n                isDefault: false\r\n            },\r\n            // Component lifecycle management\r\n            componentMounted: false,\r\n            pendingUpdates: [],\r\n            // Group widget preview\r\n            groupIframeError: false\r\n        }\r\n    },\r\n    computed: {\r\n        /**\r\n         * Determine if widget is in extra-wide mode\r\n         * @returns {boolean} True if extra-wide\r\n         */\r\n        isExtraWide() {\r\n            return this.state && (this.state.extraWide === true || this.state.extraWide === 'true');\r\n        },\r\n        \r\n        /**\r\n         * Calculate preview iframe height\r\n         * @returns {string} CSS height value\r\n         */\r\n        previewHeight() {\r\n            if (!this.state || !this.state.iframeHeight || this.state.iframeHeight === '0') {\r\n                return '100%';\r\n            } else {\r\n                return parseInt(this.state.iframeHeight) + 'px';\r\n            }\r\n        },\r\n\r\n        /**\r\n         * Calculate group widget preview iframe height\r\n         * @returns {string} CSS height value\r\n         */\r\n        groupPreviewHeight() {\r\n            if (!this.groupWidgetForm.height || this.groupWidgetForm.height === '0') {\r\n                return '100%';\r\n            } else {\r\n                return parseInt(this.groupWidgetForm.height) + 'px';\r\n            }\r\n        },\r\n\r\n        /**\r\n         * Group widgets by groupId for display\r\n         * @returns {Object} Grouped widgets\r\n         */\r\n        groupedWidgets() {\r\n            const grouped = {};\r\n            this.groupWidgets.forEach(widget => {\r\n                if (!grouped[widget.groupId]) {\r\n                    grouped[widget.groupId] = [];\r\n                }\r\n                grouped[widget.groupId].push(widget);\r\n            });\r\n            return grouped;\r\n        },\r\n        \r\n        /**\r\n         * Computed property for the extra-wide toggle\r\n         */\r\n        extraWideComputed: {\r\n            get() {\r\n                return this.state && this.state.extraWide === 'true';\r\n            },\r\n            set(value) {\r\n                if (this.state) {\r\n                    this.state.extraWide = value ? 'true' : 'false';\r\n                    // Update preview immediately when checkbox changes\r\n                    this.$nextTick(() => {\r\n                        const container = this.$el.querySelector('.preview-container');\r\n                        if (container) {\r\n                            container.style.width = value ? '640px' : '320px';\r\n                        }\r\n                    });\r\n                }\r\n            }\r\n        },\r\n    \r\n\t\tcolorValue() {\r\n\t\t\t// Return a default color when empty\r\n\t\t\treturn (this.state && this.state.widgetIconColor) || \"#ffffff\";\r\n\t\t}\r\n    },\r\n    mounted() {\r\n        // Defer reactive property access until next tick to ensure Vue reactivity is fully initialized\r\n        this.$nextTick(() => {\r\n            // Safely initialize typed values with state properties\r\n            this.typedUrl = this.state && this.state.iframeUrl ? this.state.iframeUrl : '';\r\n            this.typedIcon = this.state && this.state.widgetIcon ? this.state.widgetIcon : '';\r\n\r\n            // Only call updatePreview if state is available and reactive\r\n            if (this.state) {\r\n                this.updatePreview();\r\n            }\r\n\r\n            // Handle preview container width\r\n            const container = this.$el.querySelector('.preview-container');\r\n            if (container && this.state && (this.state.extraWide === 'true' || this.state.extraWide === true)) {\r\n                container.style.width = '640px';\r\n            }\r\n\r\n            // Listen for actual CSP errors in the console\r\n            window.addEventListener('securitypolicyviolation', this.handleCSPViolation);\r\n\r\n            // Check if iframe loads correctly after a timeout\r\n            if (this.state && this.state.iframeUrl) {\r\n                setTimeout(() => {\r\n                    this.checkIframeLoaded();\r\n                }, 3000);\r\n            }\r\n\r\n            // Load group data\r\n            this.loadGroupData();\r\n\r\n            // Mark component as mounted for safe reactivity updates\r\n            this.componentMounted = true;\r\n        });\r\n    },\r\n\tbeforeDestroy() {\r\n\t\tif (this.urlUpdateTimer) {\r\n\t\t\tclearTimeout(this.urlUpdateTimer);\r\n\t\t}\r\n\t\tif (this.iconUpdateTimer) {\r\n\t\t\tclearTimeout(this.iconUpdateTimer);\r\n\t\t}\r\n        window.removeEventListener('securitypolicyviolation', this.handleCSPViolation);\r\n\t},\r\n\r\n    watch: {\r\n        'state.iframeUrl': function(newUrl, oldUrl) {\r\n            if (!this.state) return;\r\n\r\n            if (newUrl !== oldUrl) {\r\n                this.iframeError = false;\r\n\r\n                // Attempt to reload iframe with new URL\r\n                this.$nextTick(() => {\r\n                    const iframe = this.$el.querySelector('iframe');\r\n                    if (iframe) {\r\n                        // Force reload by updating src\r\n                        iframe.src = newUrl;\r\n                    }\r\n                });\r\n            }\r\n        }\r\n    },\r\n\r\n    methods: {\r\n    \r\n        /**\r\n        * Handle color picker changes\r\n        * \r\n        * Updates the icon color when the color picker value changes and\r\n        * triggers a debounced update to prevent excessive reloads.\r\n        * \r\n        * @param {Event} event - Color input change event\r\n        */\r\n    \tupdateColor(event) {\r\n        \tthis.state.widgetIconColor = event.target.value;\r\n        \tthis.debounceIconUpdate();\r\n    \t},\r\n\r\n        handleUrlInput(event) {\r\n            // Update internal value immediately (non-reactive)\r\n            this.typedUrl = event.target.value;\r\n            // Call debounce function\r\n            this.debounceUrlUpdate();\r\n        },\r\n\r\n        /**\r\n         * Debounce URL updates to prevent excessive iframe reloads\r\n         */\r\n        debounceUrlUpdate() {\r\n\r\n            // Clear any existing timer\r\n            if (this.urlUpdateTimer) {\r\n                clearTimeout(this.urlUpdateTimer);\r\n            }\r\n            \r\n            // Only store the input in this.typedUrl (non-reactive property)\r\n            this.urlUpdateTimer = setTimeout(() => {\r\n\r\n                // Only after the full 500ms, update state and UI\r\n                this.iframeError = false;\r\n                this.state.iframeUrl = this.typedUrl;\r\n            }, 500);\r\n        },\r\n\r\n        handleCSPViolation(e) {\r\n            if (e.blockedURI && this.state.iframeUrl && \r\n                (e.blockedURI === this.state.iframeUrl || \r\n                this.state.iframeUrl.startsWith(e.blockedURI))) {\r\n                this.iframeError = true;\r\n            }\r\n        },\r\n\r\n\t\t/**\r\n\t\t * Debounce icon updates to prevent excessive API requests\r\n\t\t */\r\n\t\tdebounceIconUpdate() {\r\n\t\t\t// Clear any existing timer\r\n\t\t\tif (this.iconUpdateTimer) {\r\n\t\t\t\tclearTimeout(this.iconUpdateTimer);\r\n\t\t\t}\r\n\t\t\t\r\n\t\t\t// Set a new timer\r\n\t\t\tthis.iconUpdateTimer = setTimeout(() => {\r\n\t\t\t\tthis.state.widgetIcon = this.typedIcon;  // Only update state after timeout\r\n\t\t\t\tthis.validateIcon();\r\n\t\t\t}, 500); // 500ms debounce time\r\n\t\t},\r\n    \r\n\t\t/**\r\n\t\t * Validate icon by sending a GET request\r\n\t\t */\r\n\t\tvalidateIcon() {\r\n\t\t\tif (!this.state.widgetIcon || !this.state.widgetIcon.startsWith('si:')) {\r\n\t\t\t\tthis.updatePreview();\r\n\t\t\t\treturn;\r\n\t\t\t}\r\n\t\t\t\r\n\t\t\tconst iconName = this.state.widgetIcon.substring(3).toLowerCase();\r\n\t\t\tlet proxyUrl = generateUrl('/apps/iframewidget/proxy-icon/' + encodeURIComponent(iconName));\r\n\t\t\t\r\n\t\t\tif (this.state.widgetIconColor) {\r\n\t\t\t\tproxyUrl += '?color=' + encodeURIComponent(this.state.widgetIconColor.replace('#', ''));\r\n\t\t\t}\r\n\t\t\t\r\n\t\t\t// Check if the icon exists using our proxy\r\n\t\t\taxios.get(proxyUrl)\r\n\t\t\t\t.then(response => {\r\n\t\t\t\t\tif (response.data.exists) {\r\n\t\t\t\t\t\tthis.updatePreview();\r\n\t\t\t\t\t} else {\r\n\t\t\t\t\t\tconsole.warn(`Icon not found: ${iconName}`);\r\n\t\t\t\t\t\tshowError(t('iframewidget', 'Icon not found: {name}', {name: iconName}));\r\n\t\t\t\t\t}\r\n\t\t\t\t})\r\n\t\t\t\t.catch(error => {\r\n\t\t\t\t\tconsole.warn(`Icon not found: ${iconName}`);\r\n\t\t\t\t\tshowError(t('iframewidget', 'Icon not found: {name}', {name: iconName}));\r\n\t\t\t\t});\r\n\t\t},\r\n    \r\n        /**\r\n         * Generate URL for widget icon\r\n         * @param {string} iconName - Icon name, optionally with si: prefix\r\n         * @param {string} color - Optional hex color for the icon\r\n         * @returns {string} Complete icon URL\r\n         */\r\n        getIconUrl(iconName, color = null) {\r\n            if (!iconName) {\r\n                // Choose fallback based on theme\r\n                const prefersDarkMode = window.matchMedia && \r\n                                    window.matchMedia('(prefers-color-scheme: dark)').matches;\r\n                return prefersDarkMode ? \r\n                    OC.filePath('iframewidget', 'img', 'iframewidget-dark.svg') : \r\n                    OC.filePath('iframewidget', 'img', 'iframewidget.svg');\r\n            }\r\n            \r\n            // Handle Simple Icons format (si:iconname)\r\n            if (iconName.startsWith('si:')) {\r\n                const simpleIconName = iconName.substring(3).toLowerCase();\r\n                \r\n                // Use the simpleicons.org CDN with color support\r\n                if (color) {\r\n                    // Remove # from hex color if present\r\n                    color = color.replace('#', '');\r\n                    return `https://cdn.simpleicons.org/${simpleIconName}/${color}`;\r\n                }\r\n                \r\n                return `https://cdn.simpleicons.org/${simpleIconName}`;\r\n            }\r\n            \r\n            // Fallback for invalid format\r\n            const prefersDarkMode = window.matchMedia && \r\n                                window.matchMedia('(prefers-color-scheme: dark)').matches;\r\n            return prefersDarkMode ? \r\n                OC.filePath('iframewidget', 'img', 'iframewidget-dark.svg') : \r\n                OC.filePath('iframewidget', 'img', 'iframewidget.svg');\r\n        },\r\n\r\n\t\t/**\r\n\t\t * Handle errors loading iframe content\r\n\t\t * @param {Event} event - Error event from iframe\r\n\t\t */\r\n\t\thandleIframeError(event) {\r\n\t\t\tconsole.warn('Failed to load iframe content: ', event);\r\n\t\t\tthis.iframeError = true;\r\n\t\t},\r\n\r\n\t\t/**\r\n\t\t * Handle errors loading group widget iframe content\r\n\t\t * @param {Event} event - Error event from iframe\r\n\t\t */\r\n\t\thandleGroupIframeError(event) {\r\n\t\t\tconsole.warn('Failed to load group widget iframe content: ', event);\r\n\t\t\tthis.groupIframeError = true;\r\n\t\t},\r\n    \r\n\t\t/**\r\n\t\t * Checks if iframe content can be accessed\r\n\t\t * Detects Content Security Policy (CSP) violations by trying to access iframe DOM\r\n\t\t */\r\n        checkIframeLoaded() {\r\n            const iframe = this.$el.querySelector('iframe');\r\n            if (!iframe || !this.config.iframeUrl) return;\r\n            \r\n            this.isLoading = true;\r\n            \r\n            // Set up a visibility check instead of content access\r\n            const timeoutDuration = 5000;\r\n            const loadTimeout = setTimeout(() => {\r\n                // If iframe is visible and has non-zero dimensions, consider it loaded\r\n                if (iframe.offsetWidth > 0 && iframe.offsetHeight > 0) {\r\n                    this.iframeError = false;\r\n                } else {\r\n                    // Only show error if iframe is not visible after timeout\r\n                    this.iframeError = true;\r\n                }\r\n                this.isLoading = false;\r\n            }, timeoutDuration);\r\n            \r\n            // Clear timeout when iframe loads\r\n            iframe.onload = () => {\r\n                clearTimeout(loadTimeout);\r\n                this.iframeError = false;\r\n                this.isLoading = false;\r\n            };\r\n            \r\n            // Also clear the error if iframe becomes visible\r\n            const visibilityCheck = setInterval(() => {\r\n                if (iframe.offsetWidth > 0 && iframe.offsetHeight > 0) {\r\n                    clearInterval(visibilityCheck);\r\n                    this.iframeError = false;\r\n                    this.isLoading = false;\r\n                }\r\n            }, 500);\r\n            \r\n            // Clean up interval after max time\r\n            setTimeout(() => clearInterval(visibilityCheck), timeoutDuration + 1000);\r\n        },\r\n\r\n    \r\n        /**\r\n         * Handle errors loading icons\r\n         * @param {Event} event - Error event\r\n         */\r\n\t\thandleIconError(event) {\r\n    \t\tconsole.warn(`Icon not found or blocked by CSP: ${event.target.alt}`);\r\n    \r\n    \t\t// Choose appropriate fallback based on theme\r\n    \t\tconst prefersDarkMode = window.matchMedia && \r\n        \t\twindow.matchMedia('(prefers-color-scheme: dark)').matches;\r\n    \r\n    \t\tconst fallbackIcon = prefersDarkMode ? \r\n        \t\tOC.filePath('iframewidget', 'img', 'iframewidget-dark.svg') : \r\n        \t\tOC.filePath('iframewidget', 'img', 'iframewidget.svg');\r\n    \r\n   \t\t\t// Replace with fallback\r\n    \t\tevent.target.src = fallbackIcon;\r\n\t\t},\r\n        \r\n        /**\r\n         * Clear icon color\r\n         */\r\n        clearColor() {\r\n            this.state.widgetIconColor = '';\r\n            this.$forceUpdate();\r\n            this.updatePreview();\r\n        },\r\n        \r\n        /**\r\n         * Update the preview display\r\n         */\r\n        updatePreview() {\r\n            // Guard against undefined state\r\n            if (!this.state) return;\r\n\r\n            this.$nextTick(() => {\r\n                const previewHeader = this.$el.querySelector('.preview-header h2');\r\n                if (previewHeader) {\r\n                    // Remove default icon or hide it when custom icon exists\r\n                    const defaultIcon = previewHeader.querySelector('.icon-iframewidget');\r\n\r\n                    if (this.state.widgetIcon) {\r\n                        // Hide default icon when custom icon is used\r\n                        if (defaultIcon) defaultIcon.style.display = 'none';\r\n\r\n                        // Create/update custom icon\r\n                        const iconEl = previewHeader.querySelector('.widget-icon') ||\r\n                                    document.createElement('span');\r\n                        iconEl.className = 'widget-icon';\r\n\r\n                        // Get image element\r\n                        let imgEl = iconEl.querySelector('img');\r\n                        if (!imgEl) {\r\n                            imgEl = document.createElement('img');\r\n                            imgEl.addEventListener('error', this.handleIconError);\r\n                            iconEl.appendChild(imgEl);\r\n                        }\r\n\r\n                        // Update src with color parameter\r\n                        imgEl.src = this.getIconUrl(\r\n                            this.state.widgetIcon,\r\n                            this.state.widgetIconColor\r\n                        );\r\n                        imgEl.alt = this.state.widgetIcon;\r\n\r\n                        // Set size\r\n                        imgEl.style.width = '32px';\r\n                        imgEl.style.height = '32px';\r\n\r\n                        // Remove all filters since we're using direct color\r\n                        imgEl.classList.remove('icon-dark', 'icon-light');\r\n                        imgEl.style.filter = '';\r\n\r\n                        // Insert at beginning if not already present\r\n                        if (!previewHeader.querySelector('.widget-icon')) {\r\n                            previewHeader.insertBefore(iconEl, previewHeader.firstChild);\r\n                        }\r\n                    } else {\r\n                        // Show default icon when no custom icon\r\n                        if (defaultIcon) defaultIcon.style.display = '';\r\n\r\n                        // Remove custom icon\r\n                        const iconEl = previewHeader.querySelector('.widget-icon');\r\n                        if (iconEl) iconEl.remove();\r\n                    }\r\n                }\r\n            });\r\n        },\r\n        \r\n        /**\r\n         * Load group widgets and available groups\r\n         */\r\n        loadGroupData() {\r\n            this.loadGroupWidgets();\r\n            this.loadAvailableGroups();\r\n        },\r\n        \r\n        /**\r\n         * Load configured group widgets\r\n         */\r\n        loadGroupWidgets() {\r\n            const url = generateUrl('/apps/iframewidget/group-widgets');\r\n            console.log('Loading group widgets from:', url);\r\n            axios.get(url)\r\n                .then(response => {\r\n                    console.log('Group widgets response:', response.data);\r\n                    this.groupWidgets = response.data || [];\r\n                    console.log('Group widgets set to:', this.groupWidgets);\r\n                    this.$forceUpdate(); // Force Vue to re-render\r\n                })\r\n                .catch(error => {\r\n                    console.error('Failed to load group widgets:', error);\r\n                    this.groupWidgets = [];\r\n                });\r\n        },\r\n        \r\n        /**\r\n         * Load available groups\r\n         */\r\n        loadAvailableGroups() {\r\n            const url = generateUrl('/apps/iframewidget/groups');\r\n            axios.get(url)\r\n                .then(response => {\r\n                    this.availableGroups = response.data || [];\r\n                })\r\n                .catch(error => {\r\n                    console.error('Failed to load groups:', error);\r\n                    this.availableGroups = [];\r\n                });\r\n        },\r\n        \r\n        /**\r\n         * Show add group widget dialog\r\n         */\r\n        showAddGroupDialog() {\r\n            this.isEditing = false;\r\n            this.selectedGroupId = '';\r\n            this.editingWidgetId = '';\r\n            this.groupWidgetForm = {\r\n                id: '',\r\n                title: '',\r\n                icon: '',\r\n                iconColor: '',\r\n                url: '',\r\n                height: '',\r\n                extraWide: false,\r\n                isDefault: false\r\n            };\r\n            this.groupIframeError = false;\r\n            this.showGroupDialog = true;\r\n        },\r\n        \r\n        /**\r\n         * Hide group widget dialog\r\n         */\r\n        hideGroupDialog() {\r\n            this.showGroupDialog = false;\r\n            this.isEditing = false;\r\n            this.selectedGroupId = '';\r\n            this.editingWidgetId = '';\r\n        },\r\n        \r\n        /**\r\n         * Edit existing group widget\r\n         */\r\n        editGroupWidget(widget) {\r\n            this.isEditing = true;\r\n            this.selectedGroupId = widget.groupId;\r\n            this.editingWidgetId = widget.id;\r\n            this.groupWidgetForm = {\r\n                id: widget.id,\r\n                title: widget.title || '',\r\n                icon: widget.icon || '',\r\n                iconColor: widget.iconColor || '',\r\n                url: widget.url || '',\r\n                height: widget.height || '',\r\n                extraWide: widget.extraWide === 'true' || widget.extraWide === true,\r\n                isDefault: widget.isDefault || false\r\n            };\r\n            this.groupIframeError = false;\r\n            this.showGroupDialog = true;\r\n        },\r\n        \r\n        /**\r\n         * Save group widget\r\n         */\r\n        saveGroupWidget() {\r\n            if (!this.selectedGroupId) {\r\n                showError(t('iframewidget', 'Please select a group'));\r\n                return;\r\n            }\r\n\r\n            const url = generateUrl('/apps/iframewidget/group-widgets');\r\n            const data = {\r\n                id: this.editingWidgetId || undefined,\r\n                groupId: this.selectedGroupId,\r\n                ...this.groupWidgetForm\r\n            };\r\n\r\n            console.log('Saving group widget:', data);\r\n\r\n            axios.post(url, data, {\r\n                headers: {\r\n                    'Content-Type': 'application/json',\r\n                    'requesttoken': OC.requestToken,\r\n                    'OCS-APIRequest': 'true',\r\n                    'X-Requested-With': 'XMLHttpRequest'\r\n                }\r\n            })\r\n                .then(response => {\r\n                    console.log('Save response:', response);\r\n                    showSuccess(t('iframewidget', 'Group widget saved'));\r\n                    this.hideGroupDialog();\r\n                    // Add a small delay before reloading to ensure backend has processed\r\n                    setTimeout(() => {\r\n                        console.log('Reloading group widgets...');\r\n                        this.loadGroupWidgets();\r\n                    }, 500);\r\n                })\r\n                .catch(error => {\r\n                    console.error('Save error:', error);\r\n                    console.error('Error response:', error.response);\r\n                    showError(t('iframewidget', 'Could not save group widget'));\r\n                    console.error(error);\r\n                });\r\n        },\r\n        \r\n        /**\r\n         * Delete group widget\r\n         */\r\n        deleteGroupWidget(widgetId) {\r\n            if (!confirm(t('iframewidget', 'Are you sure you want to delete this group widget?'))) {\r\n                return;\r\n            }\r\n\r\n            const url = generateUrl('/apps/iframewidget/group-widgets/' + encodeURIComponent(widgetId));\r\n            axios.delete(url)\r\n                .then(() => {\r\n                    showSuccess(t('iframewidget', 'Group widget deleted'));\r\n                    this.loadGroupWidgets();\r\n                })\r\n                .catch(error => {\r\n                    showError(t('iframewidget', 'Could not delete group widget'));\r\n                    console.error(error);\r\n                });\r\n        },\r\n        \r\n        /**\r\n         * Handle group widget color picker changes\r\n         * \r\n         * Updates the group widget icon color when the color picker value changes\r\n         * \r\n         * @param {Event} event - Color input change event\r\n         */\r\n        updateGroupColor(event) {\r\n            this.groupWidgetForm.widgetIconColor = event.target.value;\r\n            this.$forceUpdate();\r\n        },\r\n\r\n        /**\r\n         * Add a new widget to an existing group\r\n         */\r\n        addWidgetToGroup(groupId) {\r\n            this.isEditing = false;\r\n            this.selectedGroupId = groupId;\r\n            this.editingWidgetId = '';\r\n            this.groupWidgetForm = {\r\n                id: '',\r\n                title: '',\r\n                icon: '',\r\n                iconColor: '',\r\n                url: '',\r\n                height: '',\r\n                extraWide: false,\r\n                isDefault: false\r\n            };\r\n            this.groupIframeError = false;\r\n            this.showGroupDialog = true;\r\n        },\r\n\r\n        /**\r\n         * Set a widget as the default for its group\r\n         */\r\n        setAsDefault(widgetId) {\r\n            // Find the widget in the groupWidgets array\r\n            let widgetToUpdate = null;\r\n            for (const groupWidgets of Object.values(this.groupedWidgets)) {\r\n                widgetToUpdate = groupWidgets.find(widget => widget.id === widgetId);\r\n                if (widgetToUpdate) break;\r\n            }\r\n\r\n            if (!widgetToUpdate) {\r\n                showError(t('iframewidget', 'Widget not found'));\r\n                return;\r\n            }\r\n\r\n            const url = generateUrl('/apps/iframewidget/group-widgets');\r\n            const data = {\r\n                id: widgetId,\r\n                groupId: widgetToUpdate.groupId,\r\n                title: widgetToUpdate.title || '',\r\n                icon: widgetToUpdate.icon || '',\r\n                iconColor: widgetToUpdate.iconColor || '',\r\n                url: widgetToUpdate.url || '',\r\n                height: widgetToUpdate.height || '',\r\n                extraWide: widgetToUpdate.extraWide || false,\r\n                isDefault: true\r\n            };\r\n\r\n            axios.post(url, data, {\r\n                headers: {\r\n                    'Content-Type': 'application/json',\r\n                    'requesttoken': OC.requestToken,\r\n                    'OCS-APIRequest': 'true',\r\n                    'X-Requested-With': 'XMLHttpRequest'\r\n                }\r\n            })\r\n                .then(() => {\r\n                    showSuccess(t('iframewidget', 'Widget set as default'));\r\n                    this.loadGroupWidgets();\r\n                })\r\n                .catch(error => {\r\n                    showError(t('iframewidget', 'Could not set widget as default'));\r\n                    console.error(error);\r\n                });\r\n        },\r\n\r\n        /**\r\n         * Get display name for a group\r\n         */\r\n        getGroupDisplayName(groupId) {\r\n            const group = this.availableGroups.find(g => g.id === groupId);\r\n            return group ? (group.displayName || group.id) : groupId;\r\n        },\r\n        \r\n        /**\r\n         * Save settings to the server\r\n         */\r\n        saveSettings() {\r\n            this.loading = true;\r\n            const url = generateUrl('/apps/iframewidget/config');\r\n    \r\n            axios.post(url, this.state, {\r\n                headers: {\r\n                    'Content-Type': 'application/json',\r\n                    'requesttoken': OC.requestToken,\r\n                    'OCS-APIRequest': 'true',\r\n                    'X-Requested-With': 'XMLHttpRequest'\r\n                }\r\n            })\r\n            .then(() => {\r\n                this.loading = false;\r\n                showSuccess(t('iframewidget', 'Settings saved'));\r\n            })\r\n            .catch((error) => {\r\n                this.loading = false;\r\n                showError(t('iframewidget', 'Could not save settings'));\r\n                console.error(error);\r\n            });\r\n        }\r\n    }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n/* Form layout */\r\n.iframewidget-grid-form {\r\n    display: grid;\r\n    margin-top: 30px;\r\n}\r\n\r\n.iframewidget-grid-form label:not(:first-child) {\r\n    margin-top: 20px;\r\n}\r\n\r\ninput {\r\n    max-width: 400px;\r\n    width: 100%;\r\n}\r\n\r\n/* Checkbox styles */\r\n.checkbox-container {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: flex-start;\r\n}\r\n\r\n.checkbox-container input[type=\"checkbox\"] {\r\n    position: static;\r\n    display: block;\r\n    height: 16px;\r\n    width: 16px;\r\n    margin: 0;\r\n    opacity: 1;\r\n    cursor: pointer;\r\n}\r\n\r\n.checkbox-container .checkbox-icon {\r\n    display: none; \r\n}\r\n\r\n.checkbox-container input[type=\"checkbox\"]:checked {\r\n    background-color: var(--color-primary);\r\n    border-color: var(--color-primary);\r\n}\r\n\r\n/* Group widget styles */\r\n.group-widgets-list {\r\n    margin-top: 20px;\r\n}\r\n\r\n.group-section {\r\n    border: 1px solid var(--color-border);\r\n    border-radius: 8px;\r\n    margin-bottom: 20px;\r\n    padding: 16px;\r\n    background: var(--color-main-background);\r\n}\r\n\r\n.group-header {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    margin-bottom: 12px;\r\n    padding-bottom: 8px;\r\n    border-bottom: 1px solid var(--color-border);\r\n}\r\n\r\n.group-header h4 {\r\n    margin: 0;\r\n    color: var(--color-text-maxcontrast);\r\n}\r\n\r\n.widgets-in-group {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 12px;\r\n}\r\n\r\n.group-widget-item {\r\n    border: 1px solid var(--color-border);\r\n    border-radius: 6px;\r\n    padding: 12px;\r\n    background: var(--color-background-dark);\r\n    transition: border-color 0.2s ease;\r\n}\r\n\r\n.group-widget-item.is-default {\r\n    border-color: var(--color-primary);\r\n    background: var(--color-primary-light);\r\n}\r\n\r\n.widget-header {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    margin-bottom: 8px;\r\n}\r\n\r\n.widget-title {\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 8px;\r\n    font-weight: 600;\r\n    color: var(--color-text-maxcontrast);\r\n}\r\n\r\n.default-indicator {\r\n    color: var(--color-primary);\r\n    font-size: 16px;\r\n}\r\n\r\n.widget-actions {\r\n    display: flex;\r\n    gap: 8px;\r\n}\r\n\r\n.widget-actions .button {\r\n    padding: 4px 8px;\r\n    font-size: 12px;\r\n}\r\n\r\n.widget-details {\r\n    font-size: 14px;\r\n    color: var(--color-text-light);\r\n}\r\n\r\n.widget-details p {\r\n    margin: 4px 0;\r\n}\r\n\r\n.help-text {\r\n    display: block;\r\n    margin-top: 4px;\r\n    font-size: 12px;\r\n    color: var(--color-text-light);\r\n}\r\n\r\n/* Button styles */\r\n.button.small {\r\n    padding: 4px 8px;\r\n    font-size: 12px;\r\n    min-height: 28px;\r\n}\r\n\r\n.button.button-danger {\r\n    background-color: var(--color-error);\r\n    border-color: var(--color-error);\r\n    color: white;\r\n}\r\n\r\n.button.button-danger:hover {\r\n    background-color: var(--color-error-hover);\r\n    border-color: var(--color-error-hover);\r\n}\r\n\r\n.iframewidget-logo {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n}\r\n\r\n.iframewidget-logo a {\r\n    text-decoration: none;\r\n    color: inherit;\r\n}\r\n\r\n.iframewidget-logo a.logo-link {\r\n    display: block;\r\n}\r\n\r\n.iframewidget-logo a:hover .iframewidget-version {\r\n    text-decoration: underline;\r\n    color: var(--color-primary-element);\r\n}\r\n\r\n.iframewidget-logo-image {\r\n    width: 60px;\r\n    height: 60px;\r\n    border-radius: 5px;\r\n    transition: transform 0.2s ease, box-shadow 0.2s ease;\r\n}\r\n\r\n.iframewidget-logo-image:hover {\r\n    transform: scale(1.05);\r\n    filter: drop-shadow(1px -1px 3px rgba(0, 0, 0, 0.5));\r\n}\r\n\r\n.iframewidget-version {\r\n    font-size: 12px;\r\n    color: var(--color-text-maxcontrast);\r\n    margin-top: 5px;\r\n}\r\n\r\n/* Layout container */\r\n.iframewidget-admin-container {\r\n    display: flex;\r\n    flex-wrap: wrap;\r\n    gap: 20px;\r\n    margin-top: 20px;\r\n}\r\n\r\n.iframewidget-admin-form {\r\n    flex: 0.5;\r\n    min-width: 330px;\r\n}\r\n\r\n.iframewidget-admin-preview {\r\n    flex: 1;\r\n    min-width: 320px;\r\n    max-width: 640px;\r\n}\r\n\r\n/* Preview styles */\r\n.preview-container {\r\n    border: 1px solid var(--color-border);\r\n    border-radius: var(--border-radius-container-large);\r\n    overflow: hidden;\r\n    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);\r\n    background-color: var(--color-main-background-blur);\r\n    transition: width 0.3s ease;\r\n}\r\n\r\n.preview-header {\r\n    display: flex;\r\n    align-items: center;\r\n    padding: 16px;\r\n    background-color: var(--color-main-background-blur);\r\n}\r\n\r\n.preview-header h2 {\r\n    display: block;\r\n    align-items: center;\r\n    flex-grow: 1;\r\n    margin: 0;\r\n    font-size: 20px;\r\n    font-weight: bold;\r\n    padding: 16px 8px;\r\n    height: 56px;\r\n    white-space: nowrap;\r\n    overflow: hidden;\r\n    text-overflow: ellipsis;\r\n}\r\n\r\n.preview-header span:first-child {\r\n    width: 16px;\r\n    height: 16px;\r\n    background-size: 16px;\r\n    margin-right: 8px;\r\n}\r\n\r\n.preview-header span {\r\n    background-size: 32px!important;\r\n    width: 32px!important;\r\n    height: 32px!important;\r\n    background-position: center;\r\n    float: left;\r\n    margin-top: -6px;\r\n}\r\n\r\n.preview-content {\r\n    margin: 0 16px 16px 16px;\r\n    position: relative;\r\n    height: 424px;\r\n}\r\n\r\n.preview-empty {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    height: 200px;\r\n    padding: 20px;\r\n    color: var(--color-text-maxcontrast);\r\n    text-align: center;\r\n}\r\n\r\n.preview-frame {\r\n    width: 100%;\r\n    height: 100%;\r\n    border: none;\r\n    min-height: 50px;\r\n}\r\n\r\n.preview-title {\r\n    font-size: 1rem;\r\n    font-weight: bold;\r\n    margin-bottom: 10px;\r\n    margin-top: 0;\r\n}\r\n\r\n/* Empty title styles */\r\n.preview-header.preview-title-empty {\r\n    height: 10px !important;\r\n    padding: 0 !important;\r\n}\r\n\r\n.preview-header.preview-title-empty h2 {\r\n    display: none !important;\r\n}\r\n\r\n.preview-content.preview-title-empty {\r\n    height: