\n    padding: 0;\r\n}\r\n\r\ninput[type=\"color\"]::-webkit-color-swatch {\r\n    border: none;\r\n    border-radius: 50%;\r\n}\r\n\r\n/* Firefox support */\r\ninput[type=\"color\"]::-moz-color-swatch {\r\n    border: none;\r\n    border-radius: 50%;\r\n}\r\n\r\n/* Color reset button transitions */\r\n.icon-reset-color {\r\n    min-width: 32px;\r\n    min-height: 32px;\r\n    background-color: transparent;\r\n    border: none;\r\n    cursor: pointer;\r\n    opacity: 0.7;\r\n    margin: 0!important;\r\n    border-radius: 50%;\r\n}\r\n\r\n.icon-reset-color:hover {\r\n    opacity: 1;\r\n    background-color: var(--color-background-hover);\r\n}\r\n\r\n.fade-scale-enter-from {\r\n    opacity: 0;\r\n    transform: scale(0);\r\n}\r\n\r\n.fade-scale-enter-active, \r\n.fade-scale-leave-active {\r\n    transition: all 0.3s ease;\r\n}\r\n\r\n.fade-scale-enter-from,\r\n.fade-scale-leave-to {\r\n    opacity: 0;\r\n    transform: scale(0);\r\n}\r\n\r\n.icon-preview {\r\n    display: inline-flex;\r\n    align-items: center;\r\n    margin: 0 4px;\r\n}\r\n\r\n#widget-icon {\r\n    max-width: 350px!important;\r\n}\r\n\r\n/* CSP error display styling */\r\n.widget-error.csp-error {\r\n    display: flex;\r\n    flex-direction: column;\r\n    padding: 16px;\r\n    text-align: center;\r\n    height: 200px;\r\n    justify-content: center;\r\n}\r\n\r\n/* Error title styling */\r\n.widget-error .error-title {\r\n    font-weight: bold;\r\n    margin-bottom: 8px;\r\n    color: var(--color-text-maxcontrast);\r\n}\r\n\r\n/* Error message paragraph styling */\r\n.widget-error p {\r\n    margin: 8px 0;\r\n    color: var(--color-text-maxcontrast);\r\n}\r\n\r\n/* Container for action buttons in error state */\r\n.error-actions {\r\n    margin-top: 16px;\r\n}\r\n\r\n/* Button styling for error actions */\r\n.error-actions .button {\r\n    display: inline-block;\r\n    padding: 8px 16px;\r\n    text-decoration: none;\r\n    border-radius: var(--border-radius);\r\n    background-color: var(--color-pr